* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

li {
  list-style-type: none;
}

.line {
  width: 50px;
  height: 2px;
  background: #FFFFFF;
  opacity: 0.4;
  margin-right: 20px;
  display: block;
}
#top {
  height: 100vh;
  overflow: hidden;
  position: relative;
}

#top::before {
  content: '';
  position: absolute;
  height: 100%;
  width: 400px;
  background: rgba(41, 43, 55, 0.9);
  z-index: 2;
}

#top::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
  background: linear-gradient(251.95deg, rgba(0, 0, 0, 0.76) 10.86%, rgba(0, 0, 0, 0) 78.5%);
  top: 0;
  left: 0;
}

#top .top-bg-img {
  position: absolute;
  top: 0;
  min-width: 100%;
  min-height: 100%;
}

#top h1,
#top div,
#top span,
#top p {
  position: relative;
  z-index: 11;
}

.top-summerice {
  margin-top: 150px;
}

.top-title-one {
  font-weight: bold;
  font-size: 14px;
  color: #FFFFFF;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
