  color: #FFFFFF;
  width: 46%;
}

.nav-wrapper {
  position: absolute !important;
  width: 410px;
  height: 410px;
  display: -ms-grid;
  display: grid;
  place-items: center;
  border-radius: 100%;
  border: 3px solid #9A9A9A;
  top: -100px;
  right: -100px;
}

.nav-wrapper::before {
  content: '';
  border: 3px solid #292B37;
  border-radius: 100%;
  width: 95%;
  height: 95%;
  position: absolute;
  top: 8px;
}

.the_logo {
  display: block;
  position: relative;
  top: 10px;
  left: -25px;
  z-index: 99;
}

.the_logo img {
  min-width: 100px !important;
  width: 120px;
}

.nav_links {
  position: absolute !important;
  width: 100%;
  height: 100%;
}

.nav_links a {
  font-weight: bold;
  font-size: 14px;
  color: #F6AE2D;
  font-family: 'Roboto', sans-serif;
}

.nav_links a[name="refresh"] {
  position: absolute;
  top: 125px;
  -webkit-transform: rotate(114deg);
          transform: rotate(114deg);
  left: 12px;
}

.nav_links a[name="services"] {
  position: absolute;
  top: 250px;
  -webkit-transform: rotate(70deg);
          transform: rotate(70deg);
  left: 6px;
}

.nav_links a[name="portfolio"] {
  position: absolute;
  top: 350px;
  -webkit-transform: rotate(28deg);
          transform: rotate(28deg);
  left: 85px;
}

.nav_links a[name="about_me"] {
  position: absolute;
  top: 363px;
  -webkit-transform: rotate(-16deg);
          transform: rotate(-16deg);
  left: 215px;
}
